  //  
 public ChaeckPassword(Password:string):ResponseMessage
 {
   let MessageObj:ResponseMessage={};
   if(Password == null || Password.length == 0)
   {
    MessageObj.Isok=false;
    MessageObj.Message="  !";
    return MessageObj;
   }
   else if(Password.length < 8)
   {
    MessageObj.Isok=false;
    MessageObj.Message="    8-16 !";
    return MessageObj;
   }
   else if(!/.*[a-z].*/.test(Password)|| !/.*[A-Z].*/.test(Password)||
          !/.*[0-9].*/.test(Password) || !/[^a-zA-Z0-9 ]/.test(Password))
   {
    MessageObj.Isok=false;
    MessageObj.Message="             !";
    return MessageObj;
   }
   MessageObj.Isok=true;
  return MessageObj;
}

  //  
 public CheckEmail(Email:string):ResponseMessage
 {
  let MessageObj:ResponseMessage={};
   if(Email == null || Email.length==0)
   {
    MessageObj.Isok=false;
    MessageObj.Message="  !";
    return MessageObj;
   }
   else if(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(Email))
   {
    MessageObj.Isok=true;
     return MessageObj;
   }
   MessageObj.Isok=false;
   MessageObj.Message="   !";
   return MessageObj;
 }

 //  
 public CheckPhone(Phone:string):ResponseMessage
    {
      let MessageObj:ResponseMessage={};
      if(Phone == null || Phone.length == 0)
      {
        MessageObj.Isok=false;
        MessageObj.Message="   !";
        return MessageObj;
      }
      else if(/[^0-9]/.test(Phone))
      {
        MessageObj.Isok=false;
        MessageObj.Message="     !";
        return MessageObj;
      }
      MessageObj.Isok=true;
      return MessageObj;
    }

